@keyframes arrowbottom {
    0% {
        bottom: 11%;
    }

    50% {
        bottom: 9%;
    }

    100% {
        bottom: 11%;
    }
}

@keyframes arrowtop {
    0% {
        top: 11%;
    }

    50% {
        top: 9%;
    }

    100% {
        top: 11%;
    }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
